import { motion, useAnimation } from "framer-motion";

import { Helmet } from "react-helmet";
import Layout from "../../../components/layout";
import { useEffect } from "react";
import "../ojt-experience/style.css";

export default function Experience({ data }) {
  const controls = useAnimation();
  useEffect(() => {
    controls.start((i) => ({
      opacity: 1,
      x: 0,
      z: 4,
      y: 0,
      transition: { delay: i * 0.4 },
    }));
  }, [controls]);

  return (
    <Layout>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{data.helmet.title}</title>
      </Helmet>
      <motion.div
        animate={controls}
        custom={4}
        // layoutId={"about"}
        style={{ opacity: 0 }}
        className={
          "w-full h-full  model overflow-auto  z-10 flex flex-col justify-center items-center p-2"
        }
      >
        <section className="w-full h-full flex flex-col items-center">
          <div className="mt-20" />
          <div className="max-w-6xl	p-10">
            <div className="md:flex justify-between md:pb-50">
              <h1 className="text-5xl font-bold mb-20">{data.title}</h1>
            </div>
            <div className="md:flex justify-between md:pb-50">
              <h1 className="text-3xl mb-5 font-bold italic">{data.intro}</h1>
            </div>
            {data.essay.map((e) => (
              <p
                className="text-xl mb-10 font-thin text-justify"
                dangerouslySetInnerHTML={{ __html: e }}
              />
            ))}
            <div className="mb-20 mt-20">
              <div className="md:flex justify-between md:pb-50">
                <h1 className="text-3xl mb-5 font-bold italic">
                  {data.mission_title}
                </h1>
              </div>
              <p className="text-xl mb-10 font-thin text-justify">
                {data.mission_description}
              </p>
            </div>
            <div className="mb-20">
              <div className="md:flex justify-between md:pb-50">
                <h1 className="text-3xl mb-5 font-bold italic">
                  {data.vision_title}
                </h1>
              </div>
              <p className="text-xl mb-10 font-thin text-justify">
                {data.vision_description}
              </p>
            </div>
            <div className="mb-20">
              <div className="md:flex justify-between md:pb-50">
                <h1 className="text-3xl mb-5 font-bold italic">
                  {data.company}
                </h1>
              </div>
              <p className="text-xl mb-10 font-thin text-justify">
                {data.company_description}
              </p>
            </div>
            <div className="mb-20">
              <div className="md:flex justify-between md:pb-50">
                <h1 className="text-3xl mb-5 font-bold italic">
                  ADDRESS
                </h1>
              </div>
              <p className="text-xl mb-10 font-thin text-justify">
                {data.address}
              </p>
            </div>

            <div className="md:flex justify-between md:pb-50">
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <h1 className="text-3xl mb-5 font-bold italic">EVIDENCES:</h1>
              </div>
            </div>
            <div className="overflow-auto">
              <div className="md:flex justify-between md:pb-1 grid grid-cols-2 md:grid-cols-4 gap-4">
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/1.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/2.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/3.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/4.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/5.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/6.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/7.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/8.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/9.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/10.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/11.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/12.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/13.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/14.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/15.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/16.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/17.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/18.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/19.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/20.png"
                  alt=""
                />
                <img
                  className="w-full h-auto border md:w-1/2 lg:w-1/4"
                  src="/24.png"
                  alt=""
                />
              </div>
            </div>
          </div>
        </section>
      </motion.div>
    </Layout>
  );
}
